/* shutter menu switcher */
a.shutter-menu-switcher {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -30px;
  color: #000;
  text-decoration: none;
  width: 60px;
  height: 60px;
  will-change: color, background-color;
  transition-property: color, background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.touchevents a.shutter-menu-switcher {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

a.shutter-menu-switcher::before, a.shutter-menu-switcher::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 35px;
  height: 5px;
  background-color: #6228f2;
  margin: -3px 0 0 -18px;
  border-radius: 3px;
  will-change: opacity, transform;
  opacity: 0;
  transition-property: transform, opacity;
  transition-duration: 0.2s;
  transition-delay: 0s;
  transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

a.shutter-menu-switcher > span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

a.shutter-menu-switcher > span:active, a.shutter-menu-switcher > span:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

a.shutter-menu-switcher > i {
  font-size: 0;
  line-height: 0;
  letter-spacing: -4px;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 35px;
  height: 5px;
  background-color: #6228f2;
  margin: -3px 0 0 -18px;
  border-radius: 3px;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

a.shutter-menu-switcher > i::before, a.shutter-menu-switcher > i::after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  height: 5px;
  width: 41px;
  background-color: #6228f2;
  border-radius: 3px;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

a.shutter-menu-switcher > i::before {
  top: -13px;
}

a.shutter-menu-switcher > i::after {
  top: 13px;
}

a.shutter-menu-switcher:hover {
  color: #6228f2;
}

a.shutter-menu-switcher.shutter-menu_is-open::before, a.shutter-menu-switcher.shutter-menu_is-open::after {
  transition-delay: 0.1s;
  opacity: 1;
}

a.shutter-menu-switcher.shutter-menu_is-open::before {
  transform: rotate(45deg) translateZ(0);
}

a.shutter-menu-switcher.shutter-menu_is-open::after {
  transform: rotate(-45deg) translateZ(0);
}

a.shutter-menu-switcher.shutter-menu_is-open > i {
  opacity: 0;
  transition-delay: 0s;
}

a.shutter-menu-switcher.shutter-menu_is-open > i::before, a.shutter-menu-switcher.shutter-menu_is-open > i::after {
  transition-delay: 0s;
}

a.shutter-menu-switcher.shutter-menu_is-open > i::before {
  transform: translate(0, 13px) translateZ(0);
}

a.shutter-menu-switcher.shutter-menu_is-open > i::after {
  transform: translate(0, -13px) translateZ(0);
}

/* shutter menu switcher end */
