@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";


/* === HEADER === */
.header {
  height: $header-height;
  z-index: $z-high; }

.header-holder {
  position: relative;
  height: 100%;
  @include clear; }

.header-frame {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

/* logo */
.logo {
  float: left;
  display: block;
  position: relative;
  background-image: url(../../img/logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 155px;
  color: #cccddb;
  text-indent: -9999px;
  overflow: hidden;

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi) {
    background-image: url(../../img/logo-2x.png); }

  @include media('<desk-lg') {
    background-image: url(../../img/logo-sm-2x.png);
    width: 165px;
    margin-top: -13px; }

  @include media('<tab-lg') {
    width: 93px;
    margin-top: 0;
    color: #a7a7ce; }

  &::before {
    content: "";
    display: block;
    @include ratio(155, 33); }

  a, & > span {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%; } }

/* logo end */

/* navigation */
.nav-wrap {
  margin-right: 110px; }

.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  @include font-bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  text-transform: uppercase;
  letter-spacing: 0.01em;
  @include clear;

  li {
    position: relative;
    float: left;
    margin-left: 56px; }

  a {
    color: #000;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } } }

/* navigation end */

/* shutter menu switcher */
a.shutter-menu-switcher {
  $btn-menu-size: 60px;
  $btn-menu-lines-width: 35px;
  $btn-menu-lines-height: 5px;
  $btn-menu-lines-space: 8px;
  $btn-menu-lines-color: $base-color;
  $btn-menu-lines-color--mob: $base-color;
  $btn-menu-animation-speed: $animation-speed;
  $btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

  $user-options-cross-width: $btn-menu-lines-width;
  $user-options-cross-height: $btn-menu-lines-height;
  $user-options-cross-color: $btn-menu-lines-color;
  $user-options-cross-color--mob: $btn-menu-lines-color--mob;

  // =====================

  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -#{round-float($btn-menu-size / 2, 1)};
  color: #000;
  text-decoration: none;
  width: $btn-menu-size;
  height: $btn-menu-size;
  // transition
  $prop: color, background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  // убрать подсветку при клике на тачскринах
  .touchevents & {
    user-select: none;
    -webkit-tap-highlight-color: transparent; }

  // крест
  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    @include size($user-options-cross-width, $user-options-cross-height);
    background-color: $user-options-cross-color;
    margin: -#{round($user-options-cross-height / 2)} 0 0 -#{round($user-options-cross-width / 2)};
    border-radius: 3px;
    will-change: opacity, transform;
    opacity: 0;
    transition: {
      property: transform, opacity;
      duration: $btn-menu-animation-speed;
      delay: 0s;
      timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

  // текст
  & > span {
    @include hide-text; }

  // бутерброд
  & > i {
    font-size: 0;
    line-height: 0;
    letter-spacing: -4px;
    position: absolute;
    left: 50%;
    top: 50%;
    @include size($btn-menu-lines-width, $btn-menu-lines-height);
    background-color: $btn-menu-lines-color;
    margin: -#{round($user-options-cross-height / 2)} 0 0 -#{round($user-options-cross-width / 2)};
    border-radius: 3px;
    transition: {
      property: opacity;
      duration: $btn-menu-animation-speed;
      delay: $btn-menu-animation-delay; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      right: 0;
      height: $btn-menu-lines-height;
      width: $btn-menu-lines-width + 6;
      background-color: $btn-menu-lines-color;
      border-radius: 3px;
      transition: {
        property: transform;
        duration: $btn-menu-animation-speed;
        delay: $btn-menu-animation-delay; } }

    //first line menu
    &::before {
      top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

    //third line menu
    &::after {
      top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

  // hover
  &:hover {
    color: $base-color; }

  // opened
  &.shutter-menu_is-open {

    &::before, &::after {
      transition-delay: $btn-menu-animation-delay;
      opacity: 1; }

    &::before {
      transform: rotate(45deg) translateZ(0); }

    &::after {
      transform: rotate(-45deg) translateZ(0); }

    & > i {
      opacity: 0;
      transition-delay: 0s;

      &::before, &::after {
        transition-delay: 0s; }

      &::before {
        transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

      &::after {
        transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } } }

/* shutter menu switcher end */

/* shutter menu */
.shutter-menu {
  position: fixed;
  right: 0;
  top: 0;
  width: 320px;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  $translateX: 20%;
  transform: translate($translateX, 0);
  transform: translateX($translateX);
  // transition
  $prop: opacity, visibility, transform;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &.shutter-menu_is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    $translateX: 0;
    transform: translate($translateX, 0);
    transform: translateX($translateX); } }

/* shutter menu end */

/* overlay */
.overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(black, 0.35);
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
  $prop: opacity, visibility;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  &.shutter-menu_is-open {
    opacity: 1;
    visibility: visible; } }

/* overlay end */

/* === CONTENT === */
.main {
  flex-shrink: 1;
  flex-grow: 1;
  position: relative;
  clear: both;
  z-index: 4; }

.content {
  padding-bottom: 16rem; }

/* === FOOTER === */
.footer {
  position: relative;
  color: white;
  background-color: #6228f2;
  z-index: 5; }

/* social networks in footer */
.f-soc {
  overflow: hidden;
  margin-bottom: 2rem;

  @include media('<mob-lg') {
    display: flex;
    justify-content: center; }

  &:last-child {
    margin-bottom: 0; } }

.f-soc__group {
  float: left;
  margin-left: 2rem;

  @include media('<desk-lg') {
    margin-left: 1.5rem; }

  @include media('<tab-lg') {
    margin-left: 1rem; }

  &:first-child {
    margin-left: 0; }

  & > strong {
    display: block;
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 18px;
    color: $text-color;
    opacity: 0.4;

    @include media('<tab-lg') {
      font-size: 12px; }

    @include media('<mob-lg') {
      text-align: center; } } }

.f-soc__list {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 -15px -15px 0;

  @include media('<tab-lg') {
    margin: 0 -10px -10px 0; } }

.f-soc__item {
  float: left;
  margin: 0 15px 15px 0;

  a {
    display: block;
    position: relative;
    font-size: 60px;
    @include size(1em);

    @include media('<tab-lg') {
      font-size: 40px; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      @include size(100%);
      background-image: url("../../img/soc-sprite.svg");
      background-repeat: no-repeat;
      background-size: 12em auto; } // размер файла спрайта, т.е. в спрайте 12 иконок размещенных по диагонали, 1em - 1 иконка

    &::after {
      opacity: 0; }

    // hover
    &:hover {
      &::before {
        opacity: 0; }

      &::after {
        opacity: 1; } }

    $soc-list: 'facebook', 'telegram', 'viber';
    $type-length: 4; // количество цветов одной иконки в спрайте, или через сколько единиц размещается иконка другогой социалки

    @for $i from 1 through length($soc-list) {
      $pos: $type-length - $type-length * $i * 1em; // на сколко единиц нужно сместить background

      //@debug $pos

      &.#{nth($soc-list, $i)} {
        &::before { // иконка с цветом в нормальном состоянии (фон без смещения - в спрайте такие иконки идут первыми: 1, 5, 9 и т.д.)
          $p: $pos;
          background-position: $p $p; }

        &::after { // икона с цветом при наведении (фон семстить на -1em - в спрайте такие иконки идут вторыми: 2, 6, 10 и т.д.)
          $p: $pos - 1;
          background-position: $p $p; }

        .user-form__msg__soc & {
          &::before { // иконка с другим цветом в нормальном состоянии (фон семстить на -2em - в спрайте такие иконки идут вторыми: 3, 7, 11 и т.д.)
            $p: $pos - 2;
            background-position: $p $p; }

          &::after { // иконка с другим цветом при наведении (фон семстить на -3em - в спрайте такие иконки идут вторыми: 4, 8, 12 и т.д.)
            $p: $pos - 3;
            background-position: $p $p; } } } }

    span { // Скрыть текст, но оставить видимым для озвучивающих программ
      @include hide-text; } } }

/* social networks in footer end */

/* developer */
.developer {
  line-height: 1;
  text-align: right;

  a {
    display: inline-block;
    vertical-align: top;
    color: white;
    text-decoration: none;
    // transition
    transition: color $animation-speed ease-in-out;

    svg {
      display: inline-block;
      vertical-align: top;
      @include size(70px, 15px);
      margin-right: 8px;

      path {
        fill: currentColor; } }

    & > span {
      display: inline-block;
      vertical-align: top;
      font-size: 14px;
      line-height: 15px;
      height: 15px;
      margin-top: -1px; } } }

/* developer end */

/* lacerta app */
.icon-app {
  $size: 8rem;
  position: fixed;
  right: 3.9rem;
  top: 50%;
  box-shadow: 0 3px 8px rgba(black, 0.11);
  width: $size;
  height: $size;
  margin-top: -#{round-float($size / 2, 1)};
  color: white;
  text-indent: -9999px;
  overflow: hidden;
  background-color: $highlight-color;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  z-index: $z-middle;
  // transition
  $prop: background-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .no-touchevents &:hover {
    background-color: $highlight-color--hover; }

  &:active {
    margin-top: -#{round-float($size / 2, 1) - 0.1};
    box-shadow: 0 1px 4px rgba(black, 0.11); } }

/* lacerta app end */

/* === MAIN === */
/* app offer */
.app-offer {
  position: relative;
  padding-top: 10rem;
  padding-bottom: 10rem;
  color: white;
  background-color: {}
  background: $base-color url(../../img/bg-app-offer.jpg) no-repeat fixed;
  background-size: cover; }



.app-offer-figure {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;

  img {
    max-height: 100%; } }

/* app offer end */

/* last-news */
.last-news {
  padding-top: 13rem;
  padding-bottom: 13rem;
  background-color: #f2f5ff; }

.last-news-title {
  margin-bottom: 5rem; }

$last-news-item-space: 0 3rem;
.last-news-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -#{round-float(nth($last-news-item-space, 2) / 2, 1)} 6rem;

  &:last-child {
    margin-bottom: 0; } }

.last-news-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  $width: calc(33.33% - #{nth($last-news-item-space, 2)});
  flex-basis: $width;
  min-height: 44.5rem;
  margin: 0 #{round-float(nth($last-news-item-space, 2) / 2, 1)};
  padding: 3rem;
  text-decoration: none;
  color: white;
  background-color: $base-color;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    @include linear-gradient(to top, rgba(black, 0.7) 0, rgba(black, 0) 100%); } }

.last-news-item__title {
  position: relative;
  width: 100%;
  border-bottom: 2px solid rgba(white, 0.5);
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  font-size: 2.4rem;
  line-height: 2.8rem;
  color: inherit; }

.last-news-item__text {
  position: relative;
  width: 100%;
  font-size: 1.8rem;
  line-height: 2.4rem; }

.last-news-footer {
  position: relative;
  text-align: right; }

/* last-news end */
