@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  // блокировка скролла
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked; }

  &.css-scroll-fixed.open-only-mob {
    @include media('<tab-lg') {
      @include scroll-blocked; } } }

body {
  position: relative;
  @include font-normal;
  @include font($font-size, $line-height);
  color: $text-color;
  min-width: $page-layout-min;
  background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

  // выделение текста
  *::selection {
    background-color: $base-color;
    color: white; } }

.layout {
  @if nth($page-layout-space, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space, 2)); } }

.max-wrap {
  max-width: $page-layout-width;
  @include push-auto;

  @if nth($page-layout-space, 2) != 0 {
    &.layout {
        max-width: #{$page-layout-width + nth($page-layout-space, 2) * 2}; } } }

.layout-text {
  max-width: $text-layout-width;
  @include push-auto;

  @if nth($page-layout-space, 2) != 0 {
    &.layout {
      max-width: #{$text-layout-width + nth($page-layout-space, 2) * 2}; } } }

.layout-flood,
.layout-section {
  max-width: $section-layout-width;
  @include push-auto;

  @if nth($page-layout-space, 2) != 0 {
    &.layout {
      max-width: #{$section-layout-width + nth($page-layout-space, 2) * 2}; } } }

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  margin: 0 auto;
  min-height: 100vh;
  max-width: $page-layout-width;
  position: relative; }
